import {PageContent, Pagination} from '@ra-lib/admin';
import {Tabs} from 'antd';
import config from 'src/commons/config-hoc';
import useUrlState from '@ahooksjs/use-url-state';

const {TabPane} = Tabs;

export default config({
    path: '/tab/query',
})(function TabQuery(props) {
    const [
        {
            activeKey,
            pageNum,
            pageSize,
        },
        setQueryState,
    ] = useUrlState({
        activeKey: '1',
        pageNum: 1,
        pageSize: 20,
    });

    return (
        <PageContent>
            <Tabs activeKey={activeKey} onChange={activeKey => setQueryState({activeKey})}>
                <TabPane tab="测试1" key="1">
                    面板11
                    <Pagination
                        pageNum={parseInt(pageNum, 10)}
                        pageSize={parseInt(pageSize, 10)}
                        onPageNumChange={pageNum => setQueryState({pageNum})}
                        onPageSizeChange={pageSize => setQueryState({pageSize})}
                        total={888}
                    />
                </TabPane>
                <TabPane tab="测试2" key="2">
                    面板22
                </TabPane>
            </Tabs>
        </PageContent>
    );
});
